import React, { useState, useEffect } from 'react';

import clsx from 'clsx';

import Navbar from 'react-bootstrap/Navbar';
import Image from 'react-bootstrap/Image';

import BeginPage from './BeginPage';
import FinishedPage from './FinishedPage';
import logo from '../images/logo.png';

import './App.css';

export default function App() {
  const [displayPage, setDisplayPage] = useState(() => <BeginPage onFinished={() => setDisplayPage(<FinishedPage />)} />);
  const [fade, setFade] = useState(true);

  useEffect(() => {
    setFade(true);
    setTimeout(() => setFade(false), 1000);
  }, [displayPage]);


  return (
    <div>
      <Navbar bg="dark">
        <Navbar.Brand><Image src={logo} width={200} /></Navbar.Brand>
      </Navbar>
      <div className={clsx(fade && 'app-fade-slide-in-animation')}>
        {displayPage} 
      </div>
    </div>
  );
}


