import React, { useRef, useState } from 'react';

import VerifyButton from '@passbase/button/react';
import clsx from 'clsx';

import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

import './style.css';

export default function BeginPage({ onFinished }) {
  const regNumRef = useRef();
  const [error, setError] = useState(false);

  const handleBegin = () => {
    if (!/^[0-9]{7}$/.test((regNumRef.current)))
      return setError(true);
    setError(false);
    document.getElementsByClassName("passbase-auth-element")[0].click();
  };

  return (
    <>
      <Container>
        <Row className="justify-content-center m-3">
          <Col xs="auto">
            <h1 className="display-4 text-center">Claimant Verification</h1>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col xs="auto">
            <h5 className="text-info text-center">Please Enter a 7-digit registration number</h5>
          </Col>
        </Row>
        <Row className="justify-content-center m-4">
          <Col xs="auto">
            <Form.Control
              type="text"
              className={clsx(error && 'error-form-control')}
              onChange={(e) => regNumRef.current = e.target.value}
              placeholder="eg.1234567"
            />
            {error && <p className="error-msg">Invalid</p>}
          </Col>
        </Row>
        <Row className="justify-content-center m-3">
          <Col xs="auto">
            <Button variant="info" onClick={handleBegin}>Begin</Button>
          </Col>
        </Row>
      </Container>
      <div className="begin-page-invisible-button-container">
          <VerifyButton
            apiKey="HPbkRr9Av8sAFkwu8W6X4gPj5qccLidznNjWe7S8PWqwVwFn8JM8mOQ9t19wfeGI"
            onFinish={onFinished}
            onError={onFinished}
          /> 
      </div>
    </>
  );
}


